<template>
  <div class="hello-page">
    <router-link to="/lodash" tag="h1">{{ msg }}</router-link>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to HEYEggplant\'s Blog'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1, h2 {
    font-family: 'Pattaya,sans-serif';
  }
  .hello-page {
    display: block;
    margin: 30px;
  }
</style>



